* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
}

.separator {
  display: flex;
  align-items: center;
  text-align: center;
  padding-top: 10px;
  padding-bottom: 10px;
  margin: 15px auto;
}

.separator::before,
.separator::after {
  content: "";
  flex: 1;
  border-bottom: 1px solid #000;
}

.separator:not(:empty)::before {
  margin-right: 0.25em;
}

.separator:not(:empty)::after {
  margin-left: 0.25em;
}

.centerforminput {
  position: relative;
  left: 41%;
}

.clock-container {
  margin-top: 55px;
  margin-bottom: 20px;
  background-color: #080808;
  border-radius: 5px;
  padding: 20px;
  color: white;
  width: 265px;
  white-space: nowrap;
  font-size: 25px;
  height: 130px;
  box-shadow: 3px 3px 5px 6px silver;
  opacity: 1;
  animation-name: fadeInOpacity;
  animation-iteration-count: 1;
  animation-timing-function: ease-in;
  animation-duration: 0.2s;
  margin-left: -100px;
}

.clock-label {
  color: rgba(255, 255, 255, 0.35);
  text-transform: uppercase;
  font-size: 0.5rem;
  margin-top: -5px;
  font-family: "Montserrat", "sans-serif";
}

.colon-space {
  margin-right: 15px;
}

.colon-color {
  color: rgba(255, 255, 255, 0.35);
}

.center-list-title {
  text-align: center;
}

@keyframes fadeInOpacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.wrong-input {
  animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  transform: translate3d(0, 0, 0);
  backface-visibility: hidden;
  perspective: 1000px;
}

@keyframes shake {
  10%,
  90% {
    transform: translate3d(-1px, 0, 0);
  }

  20%,
  80% {
    transform: translate3d(2px, 0, 0);
  }

  30%,
  50%,
  70% {
    transform: translate3d(-4px, 0, 0);
  }

  40%,
  60% {
    transform: translate3d(4px, 0, 0);
  }
}

.center-modalInput {
  margin-left: 90px;
}

.fade-in {
  opacity: 1;
  animation-name: fadeInOpacity;
  animation-iteration-count: 1;
  animation-timing-function: ease-in;
  animation-duration: 0.1s;
}

.title {
  font-size: 3.052em;
  letter-spacing: 0.5px;
  font-weight: 700;
  position: relative;
  top: 33%;
  left: 30%;
}

.subtitle {
  color: #5b617c;
  font-size: 1.25em;
  letter-spacing: 0.3px;
  line-height: 1.6;
  font-weight: 300;
  font-family: sans-serif;
  opacity: 0.9;
  padding: 1em 0 2em;
  position: relative;
  top: 33%;
  left: 36%;
}

.navbar-button {
  color: #494949 !important;
  text-transform: uppercase;
  text-decoration: none;
  background: #ffffff;
  padding: 15px;
  border: 4px solid #494949 !important;
  transition: all 0.4s ease 0s;
  white-space: nowrap;
}

.navbar-button:hover {
  background: lightgray;
  transition: all 0.4s ease 0s;
}

.complete-button {
  color: var(--color);
  transition: 0.25s;
}

.complete-button:hover {
  border-color: var(--hover);
  color: gray;
}

.complete-button {
  background: none;
  border: 2px solid;
  font: inherit;
  line-height: 1;
  padding: 1em 2em;
}

.text-style {
  font-family: sans-serif;
  font-weight: bold;
}

.delete-button {
  margin-left: 45px;
  margin-bottom: 10px;
}

.delete-button {
  color: var(--color);
  transition: 0.25s;
}
.delete-button:hover {
  border-color: var(--hover);
  color: gray;
}
.delete-button {
  background: none;
  border: 2px solid;
  font: inherit;
  line-height: 1;
  padding: 1em 2em;
}

.x-icon {
  cursor: pointer;
  display: inline-block;
  width: 20px;
  height: 20px;
  background: url(../images/Flat_cross_icon.svg) no-repeat;
  margin-left: 5px;
  margin-bottom: -2px;
}

.plus-icon {
  cursor: pointer;
  display: inline-block;
  width: 30px;
  height: 30px;
  background: url(../images/plus-circle.svg) no-repeat;
  margin-left: 87px;
}

.delete-animation {
  visibility: hidden;
  opacity: 1;
  transition: visibility 0s 2s, opacity 2s linear;
}
